import { newE2EPage } from '@stencil/core/testing';

describe('presto-player-spinner', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-player-spinner></presto-player-spinner>');

    const element = await page.find('presto-player-spinner');
    expect(element).toHaveClass('hydrated');
  });
});
